import {
  select,
  json
} from 'd3';


export const LoadNProcData = (dataurl) =>
  json(dataurl)
    .then((topo) => {
      const feature_array = topo.features;
      // console.log({feature_array})
      return feature_array;
    });

export const getSvg = () => {
  // Select the root svg element
  const svg = select("svg");
  return svg;

}

export const getSvgDimensions = () => {
  // Get height of root svg element
  const svg = getSvg();

  const width = +svg.attr("width");
  const height = +svg.attr("height");

  return { width: width, height: height };
}

export const ClrLgnd = (selection, props) => {
  // console.log('colorLegend called');
  const { colorValues, // Array of colors
    colorLabels, //Tile labels array
    labelRectSize, //size of color tiles
    labelSpacing, //spacing between color tiles
    labelTextOffset, //spacing between tile and label text
    onColorClick,
    selectedColorValue, // selected color value
    selectedConstituency // selected constituency
  } = props;
  
  // remove all ticks
  selection.selectAll('.tick').remove();

  // Add elements in legend body
  const groups = selection.selectAll('.tick').data(colorValues);

  // remove previuosly added elements('tick')
  groups.exit().remove();

  // Create one group for each color
  const groupsEnter = groups.enter().append('g').attr('class', 'tick');

  // Append/Update the label/color groups
  groupsEnter
    .merge(groups)
    .attr('transform', (d, i) => `translate(0, ${i * labelSpacing})`)
    .attr('opacity', d => {
      return ((!selectedColorValue || d === selectedColorValue) || selectedConstituency)
        ? 1
        : 0.2
    })
    .on('click', d => onColorClick(
      d === selectedColorValue
        ? null
        : d
    ));

  // Add the tiles
  groupsEnter.append('rect')
    .merge(groups.select('rect'))
    .attr('fill', (d, i) => colorValues[i])
    .attr('width', labelRectSize)
    .attr('height', labelRectSize);

  // console.log({colorValues, labelRectSize})

  // Add the text
  groupsEnter.append('text')
    .merge(groups.select('text'))
    .text((d, i) => colorLabels[i])
    .attr('dy', '1.2em')
    .attr('x', labelTextOffset);
};

// Get background rectangale dimensions
export const getBgRectangleDimensions = (colorValues,
  colorLabels,
  labelRectSize,
  labelTextOffset,
  labelSpacing,
  legendTitle) => {
  // Find longest label
  const longestLabel = colorLabels.reduce(function (a, b) { return a.length > b.length ? a : b; });
  const longestText = (legendTitle.length > longestLabel.length)
    ? legendTitle
    : longestLabel;

  // console.log('longestText: ' + longestText);
  const backgroundRectWidth = labelRectSize + labelTextOffset + longestText.length * 5 + labelTextOffset;
  const backgroundRectHeight = labelSpacing * (colorValues.length + 2);
  return { width: backgroundRectWidth, height: backgroundRectHeight };
};

export const dropDownMenu = (selection, props) => {
  const {
    options,
    onOptionClicked
  } = props;

  let select = selection.selectAll('select').data([null]);

  select = select.enter().append('select')
    .merge(select)
    .on('change', function() {
      onOptionClicked(this.value);
    });

  const option = select.selectAll('option').data(options);

  option.enter().append('option')
    .merge(option)
      .attr('value', d => d)
      .text(d => d);  

 };

// Create categories from Education field
export const processEduFeatures = (features) => {
  /////  Categories of education  ////
  // Doctorate: 7 
  // Post Graduate: 6
  // Graduate Professional: 5
  // Graduate: 5 
  // 12th Pass: 4
  // 10th Pass: 4 
  // 8th Pass: 4
  // 5th Pass: 4
  // Literate: 4 
  // Others: 3
  // Illiterate: 2 
  // nan

  features.forEach(element => {
    if (element.properties.Education === 'Doctorate') {
      element.properties.edu = 7;
    } else if (element.properties.Education === 'Post Graduate') {
      element.properties.edu = 6;
    } else if (element.properties.Education === 'Graduate Professional' ||
      element.properties.Education === 'Graduate') {
      element.properties.edu = 5;
    } else if (element.properties.Education === '12th Pass' ||
      element.properties.Education === '10th Pass' ||
      element.properties.Education === '8th Pass' ||
      element.properties.Education === '5th Pass' ||
      element.properties.Education === 'Literate') {
      element.properties.edu = 4;
    } else if (element.properties.Education === 'Others') {
      element.properties.edu = 3;
    } else if (element.properties.Education === 'Illiterate') {
      element.properties.edu = 2;
    } else {
      element.properties.edu = 0;
      // console.log('unexpected Education field: ' + element.properties.ST_PC ); 
    }
  });
}

// Create categories from Education field
export const processPartyFeatures = (features) => {
  // Get count of seats for all parties in an object
  let partyCount = features.reduce((p, c) => {
    let name = c.properties.Party;
    if (!p.hasOwnProperty(name)) {
      p[name] = 0;
    }
    p[name]++;
    return p;
  }, {});


  let sortedPartyCount = [];
  for (let party in partyCount) {
    sortedPartyCount.push([party, partyCount[party]]);
  }

  // Sort the array
  sortedPartyCount.sort(function (a, b) {
    return b[1] - a[1];
  });

  // get top 10 parties by seats
  sortedPartyCount = sortedPartyCount.slice(0, 10);

  const partyMap = {};
  for (var i = 0; i < sortedPartyCount.length; ++i)
    partyMap[sortedPartyCount[i][0]] = sortedPartyCount[i][1];

  return partyMap;
}

